<template>
<div class="content">
    <!-- 头部 -->
    <el-breadcrumb separator="/">
        <el-breadcrumb-item> 点播加速日志</el-breadcrumb-item>
        <!-- <el-breadcrumb-item>
        <a href="/">ip流量</a>
      </el-breadcrumb-item> -->
    </el-breadcrumb>
    <!-- 搜索 -->
    <div class="seach">
        <div class="seach_top">
            <el-input placeholder="点播IP、渠道ID、URL" v-model="input" class="input-with-select">
                <i slot="prefix" class="el-input__icon el-icon-search" @click="seachuser()"></i>
            </el-input>
            <div class="seach_top_right" @click="option_display()">
                筛选
                <i class="el-icon-caret-bottom" :class="[rotate?'fa fa-arrow-down go':'fa fa-arrow-down aa']"></i>
            </div>
        </div>
        <div v-if="optiondisplay" class="seach_bottom">
            <span>选择日期：</span>
            <el-date-picker v-model="value1" type="datetimerange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
            <div class="seach_bottom_btn">
                <el-button type="primary" plain size="mini" @click="seachuser()">确定</el-button>
                <el-button plain size="mini" @click="reset()">重置</el-button>
            </div>
        </div>
    </div>

    <!-- 表格 -->
    <div style="background: #fff;margin-top:20px;padding:20px;">
        <div style="text-align:right;padding: 10px;">
            <el-button type="primary" @click="exportExcel()">导出</el-button>
        </div>
        <el-table :data="tableData" :cell-style="rowClass" :header-cell-style="headClass" style="width: 100%">
            <el-table-column prop="ipInfo" label="点播IP" width="180"></el-table-column>
            <el-table-column prop="chanId" label="渠道ID" width="180"></el-table-column>
            <el-table-column prop="fileUrl" label="域名"></el-table-column>
            <el-table-column prop="dataFlow" label="消费流量"></el-table-column>
            <el-table-column prop="accessCnt" label="请求数"></el-table-column>
            <el-table-column prop="failCnt" label="失败请求数"></el-table-column>
            <el-table-column prop="startTS" :formatter="formatterTime" label="启用时间"></el-table-column>
            <el-table-column prop="endTS" label="结束时间" :formatter="formatterTime1"></el-table-column>
            <el-table-column prop="totalTime" label="用时"></el-table-column>
        </el-table>
    </div>
    <fenye style="float:right;margin:10px 0 0 0;" @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" :currentPage="currentPage" :pagesa="total_cnt"></fenye>
</div>
</template>

<script>
import fenye from "@/components/fenye";
import {
    query_accelerate_log
} from '../../servers/api'
import common from '../../comm/js/util'
export default {
    data() {
        return {
            rotate: false,
            errTableVisible: false,
            device_show: false,
            currentPage: 1,
            total_cnt: 0,
            value1: "",
            input: "",
            value: "",
            input1: "", //开始时间
            input2: "", //结束时间
            optiondisplay: false,
            dev_devation: {},
            sizeForm: {
                date1: "",
                date2: ""
            },
            options: [{
                    value: 0,
                    label: "电信"
                },
                {
                    value: 1,
                    label: "移动"
                },
                {
                    value: 1,
                    label: "联通"
                }
            ],
            tableData: []
        };
    },
    components: {
        fenye
    },
    mounted() {
        this.queryInfo()
    },
    methods: {
        //确定搜索

        //处理启用时间、
        formatterTime(row) {
            return this.common.getTimes(row.startTS * 1000);
        },
        //处理启用时间、
        formatterTime1(row) {
            return this.common.getTimes(row.endTS * 1000);
        },
        //查询信息
        queryInfo() {
            let startTime = 0
            let endTime = 0
            if (this.value1) {
                if (this.value1 == "") {
                     startTime = 1571465783
                     endTime = 1576736183
                } else {
                    endTime = this.value1[1].getTime() / 1000
                    startTime = this.value1[0].getTime() / 1000
                }

            } else {
                 startTime = 1571465783
                 endTime = 1576736183
            }

            let param = {
                start_ts: startTime,
                end_ts: endTime,
                chanId: this.input == "" ? "*" : this.input,
                fileUrl: "*",
                userIp: "*",
                pageNo: this.currentPage - 1,
                pageSize: 10
            }
            query_accelerate_log(param).then(res => {
                console.log(res)
                if (res.status == 0) {
                    this.tableData = res.data.list
                    this.total_cnt = res.data.totalCnt
                }
            }).catch(error => {

            })
        },

        //筛选按钮
        option_display() {
            this.optiondisplay = !this.optiondisplay;
            this.rotate = !this.rotate;
        },
        //确定搜索
        seachuser() {
            this.optiondisplay = false;
            // this.value = "";
            // this.input = "";
           
            // this.value = "";
            // this.input = "";
            this.queryInfo()
        },
        //重置
        reset() {
            this.input = "";
            this.value1 = "";
            this.currentPage=1
            console.log("重置")
            this.queryInfo()
        },
        // 表头样式设置
        headClass() {
            return "text-align: center;background:#eef1f6;";
        },
        // 表格样式设置
        rowClass() {
            return "text-align: center;";
        },
        //获取页码
        handleCurrentChange(pages) {
            console.log(pages);
            this.currentPage = pages;
            this.queryInfo()
            //this.getdata();
        },
        //获取每页数量
        handleSizeChange(pagetol) {
            console.log(pagetol);
            this.currentPage = pagetol;
            //this.getdata();
        },
        exportExcel() {
            require.ensure([], () => {
                const {
                    export_json_to_excel
                } = require("../../excel/Export2Excel.js");
                const tHeader = [
                    "用途",
                    "上下行带宽（占用）",
                    "共计使用流量",
                    "节点ID",
                    "节点ID",
                    "启用时间",
                    "使用时长",
                    "使用状态",
                    "FS应用渠道",
                    "使用者ID"
                ];
                // 上面设置Excel的表格第一行的标题
                const filterVal = [
                    "use",
                    "up_and_down_bandwidth",
                    "total_usage_flow",
                    "content_id",
                    "node_id",
                    "start_time",
                    "usage_time",
                    "status_use",
                    "ip_application_channel",
                    "user_id"
                ];
                // 上面的index、nickName、name是tableData里对象的属性
                const list = this.tableData; //把data里的tableData存到list
                const data = this.formatJson(filterVal, list);
                export_json_to_excel(tHeader, data, "IP流量");
            });
        },
        formatJson(filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        }
    }
};
</script>

<style lang="scss" scoped>
.content {

    .device_title_name {
        width: 16%;
        background: #eeeeee;
        color: #000000;
    }

    .device_title_content {
        width: 16%;
        background: #ffffff;
        color: #000000;
    }

    .tableclose {
        width: 100%;
        padding-top: 10px;
        text-align: center;
    }
}

//旋转
.aa {
    transition: all 1s;
}

.go {
    transform: rotate(-180deg);
    transition: all 1s;
}
</style>



