<template>
  <div class="copy_configuration">
    <div class="top_title">
      <span @click="goback" style="font-size: 24px;color: #202020;"
        ><i
          class="el-icon-arrow-left"
          style="color:#297AFF;font-size: 18px;margin-right:23px;font-weight: 600;"
        ></i>
        复制配置</span
      >
    </div>
    <p class="copy_title">
      复制配置允许将一个域名的配置项复制到多个域名，帮助您对域名进行批量配置
    </p>
    <div class="copy_con">
      <div>
        <el-steps :active="actives" simple>
          <el-step title="选择配置项" icon="el-icon-edit"></el-step>
          <el-step title="选择域名" icon="el-icon-upload"></el-step>
          <el-step title="完成" icon="el-icon-picture"></el-step>
        </el-steps>
      </div>

      <div v-if="actives === 1">
        <el-table
          ref="multipleTable"
          :data="futableData"
          tooltip-effect="dark"
          stripe
          style="width: 100%"
          :cell-style="rowClass"
          :header-cell-style="headClass"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column
            prop="configuration"
            label="配置项"
          ></el-table-column>
          <el-table-column
            prop="nowconfiguration"
            label="当前配置"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              <div
                v-if="scope.row.nowconfiguration == '已配置'"
                style="display: flex;justify-content: center;align-items: center;color:#333333;"
              >
                <i
                  class="iconfont icon-dian"
                  style="font-size: 36px;color:#297AFF;"
                ></i
                ><span> {{ scope.row.nowconfiguration }}</span>
              </div>
              <div
                v-else
                style="display: flex;justify-content: center;align-items: center;color:#949494;"
              >
                <i
                  class="iconfont icon-dian"
                  style="font-size: 36px;color:#828282;"
                ></i
                ><span> {{ scope.row.nowconfiguration }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope" label="操作">
              <el-button
                @click="handleClick(scope.row)"
                type="text"
                size="small"
                style="color:#547DC0;"
                v-if="scope.row.nowconfiguration == '已配置'"
                >详情</el-button
              >
              <el-button
                @click="handleClick(scope.row)"
                type="text"
                size="small"
                v-else
                >配置</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <el-button style="margin-top: 12px;" type="primary" @click="next"
          >下一步</el-button
        >
        <el-button style="margin-top: 12px;" @click="fureset">取消</el-button>
      </div>
      <div v-else-if="actives === 2">
        <div
          style="display: flex;justify-content: flex-start;align-items: center;margin: 10px 0"
        >
          <span style="margin-right:21px;color:#333333;font-size:14px;"
            >URL列表</span
          >
          <el-input
            placeholder="请输入域名"
            v-model="fuinput"
            style=" width:300px"
            class="input-with-select"
            @keyup.enter.native="onSubmit"
          ></el-input>
        </div>
        <div class="copy_prompt">
          <div class="banyuan"></div>
          <i
            class="el-icon-info"
            style="color:#6666FF;font-size:18px;margin-left:15px;"
          ></i>
          已选择
          <span>{{ nums }}</span
          >个域名，最多允许50个
          <span>点击查看</span>
        </div>
        <div>
          <el-table
            ref="multipleTable"
            :data="urllist"
            tooltip-effect="dark"
            stripe
            style="width: 100%"
            :cell-style="rowClass"
            :header-cell-style="headClass"
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="url" label="域名"></el-table-column>
          </el-table>
        </div>
        <el-button style="margin-top: 12px;" type="primary" @click="last"
          >上一步</el-button
        >
        <el-button style="margin-top: 12px;" type="primary" @click="next"
          >下一步</el-button
        >
        <el-button style="margin-top: 12px;" @click="fureset">取消</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      actives: 1,
      nums: 12,
      fuinput: "",
      urllist: [
        { url: "http://www.xxx.cn.abc" },
        { url: "http://www.xxx.cn.abc" },
        { url: "http://www.xxx.cn.abc" },
        { url: "http://www.xxx.cn.abc" },
        { url: "http://www.xxx.cn.abc" },
        { url: "http://www.xxx.cn.abc" }
      ],
      futableData: [
        {
          configuration: "回源HOST",
          nowconfiguration: "已配置"
        },
        {
          configuration: "缓存设置",
          nowconfiguration: "已配置"
        },
        {
          configuration: "缓存过期时间",
          nowconfiguration: "已配置"
        },
        {
          configuration: "自定义页面",
          nowconfiguration: "未配置"
        },
        {
          configuration: "Refer防盗链",
          nowconfiguration: "已配置"
        },
        {
          configuration: "URL鉴权",
          nowconfiguration: "已配置"
        },
        {
          configuration: "IP黑/白名单",
          nowconfiguration: "未配置"
        }
      ]
    };
  },
  methods: {
    //返回
    goback() {
      this.$router.go(-1);
    },
    //复制配置下一步
    next() {
      if (this.actives == 2) {
        const confirmText = [
          "您确定要批量复制配置吗？",
          "域名配置复制后，操作不可逆，请务必确认您的域名复制选择无误零流量宽带较大的域名，请谨慎复制；若您之前有通过工单进行过后端特殊配置（非控制台功能配置），该特殊配置将无法复制。"
        ];
        const newDatas = [];
        const h = this.$createElement;
        for (const i in confirmText) {
          newDatas.push(h("p", null, confirmText[i]));
        }

        this.$confirm("提示", {
          title: "提示",
          message: h("div", null, newDatas),
          showCancelButton: true,
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.dialupdata = false;
            this.actives = 1;
            // 请求成功之后，调用接口
            this.$message({
              type: "success",
              message: "配置成功!"
            });
          })
          .catch(() => {
            this.dialupdata = false;
            this.actives = 1;
            this.$message({
              type: "info",
              message: "已取消操作"
            });
          });
      }
      if (this.actives++ > 2) {
        if (this.active++ > 2) this.active = 0;
        return false;
      }
    },
    //复制配置上一步
    last() {
      if (this.actives-- < 1) {
        // this.actives = 0;
        return false;
      }
    },
    //复制配置全选
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //复制配置取消
    fureset() {
      this.actives = 1;
      this.dialupdata = false;
    },
    // 表头样式设置
    headClass() {
      return "text-align: center;background:#eef1f6;color:#333333;font-size:16px;";
    },
    // 表格样式设置
    rowClass() {
      return "text-align: center;";
    }
  }
};
</script>

<style lang="scss" scoped>
.copy_configuration {
  .copy_title {
    width: 1240px;
    height: 40px;
    background: rgba(224, 240, 255, 1);
    border: 1px solid rgba(97, 157, 255, 1);
    font-size: 14px;
    color: #728abd;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
    line-height: 40px;
    text-align: left;
    padding-left: 17px;
  }
  .copy_con {
    width: 1240px;
    height: 675px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 3px 0px rgba(6, 17, 36, 0.14);
    border-radius: 6px;
    margin: 0 auto;
    padding: 45px 37px;
    .copy_prompt {
      text-align: left;
      width: 632px;
      height: 50px;
      line-height: 50px;
      background: #f0f0ff;
      margin-bottom: 2px;
      border-radius: 2px;
      display: flex;
      align-items: center;
      border-radius: 2px;
      .banyuan {
        width: 5px;
        height: 100%;
        background: #6666ff;
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
      }
      span {
        font-size: 18px;
        color: #6666ff;
      }
    }
  }
}
</style>



