<template>
<div class="content">
    <el-breadcrumb separator="/">
        <el-breadcrumb-item>域名管理</el-breadcrumb-item>
        <el-breadcrumb-item>
            <a href="/">刷新预热管理</a>
        </el-breadcrumb-item>
    </el-breadcrumb>
    <div>
        <!-- 搜索 -->
        <div class="seach">
            <div class="seach_top">
                <el-input placeholder="用户ID，手机号，用户名" v-model="input" class="input-with-select" @keyup.enter.native="onSubmit">
                    <i slot="prefix" class="el-input__icon el-icon-search" @click="seachuser()"></i>
                </el-input>
                <div class="seach_top_right" @click="option_display()">
                    筛选
                    <i class="el-icon-caret-bottom" :class="[rotate?'fa fa-arrow-down go':'fa fa-arrow-down aa']"></i>
                </div>
            </div>
            <div v-if="optiondisplay" class="seach_bottom">
                <span>状态：</span>
                <el-select v-model="value" placeholder="请选择">
                    <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
                </el-select>
                <span>注册时间：</span>
                <el-date-picker v-model="value1" type="datetimerange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
                <div class="seach_bottom_btn">
                    <el-button type="primary" plain size="mini" @click="seachuser()">确定</el-button>
                    <el-button plain size="mini" @click="reset()">重置</el-button>
                </div>
            </div>
        </div>
        <!-- 表格 -->
        <div class="con_lable">
            <div style="padding:10px;text-align: left;">
                <el-button type="primary" @click="exportExcel1()">刷新</el-button>
                <el-button type="primary" @click="exportExcel1()">预热</el-button>
            </div>

            <!-- 表格 -->
            <el-table ref="multipleTable" :data="tableData" height="auto" tooltip-effect="dark" style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" :default-sort="{prop: 'date', order: 'descending'}" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column label="操作内容" prop="user_id" width="120">

                </el-table-column>
                <el-table-column prop="user_id" label="操作类型"></el-table-column>
                <el-table-column prop="user_name" label="渠道ID"></el-table-column>
                <el-table-column prop="time_create" sortable label="操作时间"></el-table-column>

                <el-table-column prop="user_tel" label="状态"></el-table-column>
                <el-table-column prop="flow" label="进度"></el-table-column>

            </el-table>
            <!-- 详情弹窗 -->
            <el-dialog title="刷新预热" :visible.sync="dialog" custom-class="customWidth" width="40%">
                <div>
                    <el-tabs v-model="activeName" @tab-click="handleClick">
                        <el-tab-pane label="刷新缓存" name="first">
                            <div class="refresh">
                                <div class="item">
                                    <div style="margin-right:20px;">刷新类型</div>
                                    <div>
                                        <el-radio v-model="radio" label="1">备选项</el-radio>
                                        <el-radio v-model="radio" label="2">备选项</el-radio>
                                    </div>
                                </div>
                                <div class="item">
                                    Filed刷新单次提交最多 10 条，使用回车换行输入，刷新任务完成时间预计为 10 分钟
                                </div>
                                <div class="item">
                                    <textarea></textarea>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="内容预热" name="second">
                            <div class="refresh">
                                <div class="item">
                                    <div style="margin-right:20px;">刷新类型</div>
                                    <div>
                                        <el-radio v-model="radio" label="1">备选项</el-radio>
                                        <el-radio v-model="radio" label="2">备选项</el-radio>
                                    </div>
                                </div>
                                <div class="item">
                                    Filed刷新单次提交最多 10 条，使用回车换行输入，刷新任务完成时间预计为 10 分钟
                                </div>
                                <div class="item">
                                    <textarea></textarea>
                                </div>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
                <div class="dialog-footer" style="margin-top:15px;text-align:center;">
                    <el-button type="primary" @click="dialog=false">确 定</el-button>
                </div>
            </el-dialog>

            <!-- 按钮 -->
            <div style="margin-top: 20px;display: flex;justify-content: space-between;align-items: center;">
                <div>
                    <el-button @click="enableuser()" type="success">启用</el-button>
                    <el-button @click="disableuser()" type="warning">禁用</el-button>
                    <el-button @click="deleateuser()" type="danger">删除</el-button>
                </div>
                <fenye style="float:right;margin:10px 0 0 0;" @fatherMethod="getpage" @fathernum="gettol" :pagesa="total_cnt"></fenye>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import fenye from "@/components/fenye";
import {
    userlist
} from "../../servers/api";
export default {
    data() {
        return {
            input: "", //搜索输入框
            value1: "",
            dialog: false,
            radio: "1",
            radioes: "1",
            activeName: "first",

            sadioes: 1,
            pagesize: 10,
            total_cnt: 1,
            form: {
                account: "",
                nickname: "",
                pwd: "",
                conpwd: "",
                actualname: "",
                tel: "",
                delivery: false,
                type: [],
                resource: "",
                desc: ""
            },
            details: {},

            formLabelWidth: "60px",
            optiondisplay: false,
            rotate: false,

            tolpage: 1,

            value: "",
            options: [{
                    value: 0,
                    label: "正常运行"
                },
                {
                    value: 1,
                    label: "已停止"
                },
                {
                    value: 2,
                    label: "配置中"
                },
                {
                    value: 3,
                    label: "配置失败"
                },
                {
                    value: 4,
                    label: "审核中"
                },
                {
                    value: 5,
                    label: "审核失败"
                }
            ],
            pager: {
                count: 0,
                page: 1,
                rows: 100
            },
            tableData: [{
                    status: "冻结",
                    user_id: "01653752",
                    user_name: "后收",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "48773416"
                },
                {
                    status: "正常",
                    user_id: "0264512",
                    user_name: "哈安万特哈",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "4876487416"
                },
                {
                    status: "正常",
                    user_id: "0634712",
                    user_name: "民工气",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "487416"
                },
                {
                    status: "正常",
                    user_id: "012",
                    user_name: "给尔皮克",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "487412476"
                },
                {
                    status: "冻结",
                    user_id: "0161362",
                    user_name: "提前而已",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "154487416"
                },
                {
                    status: "冻结",
                    user_id: "01635472",
                    user_name: "翢",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "487416"
                },
                {
                    status: "正常",
                    user_id: "07453712",
                    user_name: "特瑞特",
                    user_tel: 13501201248,
                    time_create: "2019/11/05",
                    flow: "876487416"
                }
            ]
        };
    },
    components: {
        fenye
    },
    created() {},
    mounted() {
        this.getuserlist();
    },
    methods: {
        //获取用户列表
        getuserlist() {
            let params = new Object();
            params.page = this.pager.page - 1;
            params.search = this.input;
            params.status = parseInt(this.value);
            userlist(params)
                .then(res => {
                    console.log(res);
                    if (res.status != 0) {
                        this.$message({
                            message: `${res.err_msg}`,
                            type: "error"
                        });
                    } else {
                        if (res.result.page == 0) {
                            this.pager.count =
                                res.result.cols.length * (res.result.page + 1) +
                                res.result.les_count;
                        } else {
                            this.pager.count =
                                10 +
                                res.result.cols.length * res.result.page +
                                res.result.les_count;
                        }
                        let nowArr = res.result.cols;
                        for (var i = 0; i < nowArr.length; i++) {
                            nowArr[i].time_create = this.common.getTimes(
                                parseInt(nowArr[i].time_create * 1000)
                            );
                            nowArr[i].time_update = this.common.getTimes(
                                parseInt(nowArr[i].time_update * 1000)
                            );
                            if (nowArr[i].status == 0) {
                                nowArr[i].status = "启用";
                            } else {
                                nowArr[i].status = "禁用";
                            }
                        }
                        // this.tableData = nowArr;
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //获取页码
        getpage(pages) {
            console.log(pages);
            this.tolpage = pages;
            //this.getdata();
        },
        //获取每页数量
        gettol(pagetol) {
            console.log(pagetol);
            this.pagesize = pagetol;
            //this.getdata();
        },
        //回车事件
        onSubmit() {
            console.log("enter");
        },
        //筛选按钮
        option_display() {
            console.log("******");
            this.optiondisplay = !this.optiondisplay;
            this.rotate = !this.rotate;
        },
        //确定搜索
        seachuser() {
            console.log(this.value, this.input);
            this.optiondisplay = false;
            this.value = "";
            this.input = "";
        },
        //重置
        reset() {
            this.value = "";
            this.input = "";
            console.log("重置");
        },
        //表格查看
        handleClick(row) {
            console.log(row);
            if (row.radio == "启用") {
                this.sadioes = "1";
            } else {
                this.sadioes = "2";
            }
            this.details = row;
            this.dialog = true;
        },
        // 全选
        handleSelectionChange(val) {
            console.log(val);
            this.multipleSelection = val;
        },
        // 禁用
        disableuser() {
            this.$confirm("禁用后该用户不能登陆, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                })
                .then(() => {
                    this.$message({
                        type: "success",
                        message: "操作成功!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消"
                    });
                });
        },
        //启用
        enableuser() {},
        // 删除
        deleateuser() {
            this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                })
                .then(() => {
                    this.$message({
                        type: "success",
                        message: "删除成功!"
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消删除"
                    });
                });
        },
        //导出的方法
        exportExcel1() {
            this.dialog = true
        },
        formatJson(filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        },

        // 表头样式设置
        headClass() {
            return "text-align: center;background:#eef1f6;";
        },
        // 表格样式设置
        rowClass() {
            return "text-align: center;";
        }
    }
};
</script>

<style lang="scss" scoped>


.refresh {
    width: 100%;
    display: flex;
    flex-flow: column;

    .item {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-top: 15px;

        textarea {
            width: 700px;
            height: 300px;
            border: 1px solid #DCDFE6;
            ;
            resize: none;

        }
    }
}


</style>



