<template>
<div class="content">
    <!-- 头部搜索 -->
    <div class="seach_bottom">
        <el-select v-model="value" placeholder="请选择">
            <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>

        <el-input v-model="input1" placeholder="请输入URL" style="width:220px;margin-left:20px;"></el-input>

      <el-button type="primary" size="mini" @click="seachuser()" style="margin-left:20px;">确定</el-button> 
      <!-- <el-button plain size="mini" @click="reset()">重置</el-button> --> 
    </div>
    <!-- 中间表格 -->
    <div style="background:#ffffff;margin-top:15px;padding:15px;">
        <el-table :data="tableData" border style="width: 100%;margin-top:10px;" :cell-style="rowClass" :header-cell-style="headClass">
            <el-table-column prop="domain" label="域名"></el-table-column>
            <el-table-column prop="label" label="主标签">
                <template slot-scope="scope">
                    <el-input v-model="scope.row.label" placeholder="姓名" :disabled="!scope.row.state"></el-input>
                </template>
            </el-table-column>
            <el-table-column prop="label2" label="副标签">
                <template slot-scope="scope">
                    <el-select v-model="scope.row.label2" placeholder="请选择" :disabled="!scope.row.state">
                        <el-option label="1" value="1">1</el-option>
                        <el-option label="2" value="2">2</el-option>
                    </el-select>
                </template>
            </el-table-column>
            <el-table-column label="当前配置">
                <template slot-scope="scope">
                    <el-button type="primary" size="small" @click="handleClick(scope.$index, scope.row)">{{scope.row.state==false?"修改":"保存"}}</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="width: 100%;height: auto;overflow: hidden;">
            <fenye style="float:right;margin:10px 0 0 0;" @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" :currentPage="currentPage" :pagesa="total_cnt"></fenye>
        </div>

        <div style="margin-top:15px;text-align: center;">
            <el-button type="primary" @click="onSubmit">确定</el-button>
            <el-button>取消</el-button>
        </div>

    </div>
</div>
</template>

<script>
import {
    modify_label,
    query_url
} from "../../servers/api";
import fenye from "@/components/fenye";

import {
    constants
} from 'zlib';
export default {
    data() {
        return {
            currentPage: 1,
            pagesize: 10,
            total_cnt: 0,
            value: "",
            value1: "",
            input1: '',
            options: [{
                    value: 0,
                    label: "正常运行"
                },
                {
                    value: 1,
                    label: "已停止"
                },
                {
                    value: 2,
                    label: "配置中"
                },
                {
                    value: 3,
                    label: "配置失败"
                },
                {
                    value: 4,
                    label: "审核中"
                },
                {
                    value: 5,
                    label: "审核失败"
                }
            ],
            tableData: [{
                    domain: "http://www.baidu.com",
                    label: "rjtgag",
                    label2: "1",
                    state: false
                },
                {
                    domain: "http://www.biubiubiu.com",
                    label: "krk145aeg",
                    label2: "1",
                    state: false
                },
                {
                    domain: "http://www.wangyiyun.com",
                    label: "3654451tg45e1g",
                    label2: "1",
                    state: false
                },
                {
                    domain: "http://www.souhu.com",
                    label: "1g5f4ah114g54gre",
                    label2: "2",
                    state: false
                },
                {
                    domain: "http://www.360.com",
                    label: "RQ3851G56A",
                    label2: "2",
                    state: false
                }
            ]
        };
    },
    mounted() {
        this.queryUrlList()

        //this.assembleArr()
    },
    methods: {
        seachuser(){



            this.queryUrlList()

        },
        //获取页码
        handleCurrentChange(pages) {
            console.log(pages);
            this.currentPage = pages;
            this.queryUrlList()
            //this.getdata();
        },
        //获取每页数量
        handleSizeChange(pagetol) {
            console.log(pagetol);
            this.currentPage = pagetol;
            //this.getdata();
        },
        //获取URL列表
        queryUrlList() {

            let params = new Object();
            params.page = this.currentPage - 1
            params.buser_id = ""
            params.url = this.input1
            if (this.value == "-1") {

            } else {
                params.state = this.value
            }
            if (this.value1) {
                params.end_time = this.value1[1].getTime() / 1000
                params.start_time = this.value1[0].getTime() / 1000
            } else {

            }

            query_url(params)
                .then(res => {
                    console.log(res);
                    if (res.status == 0) {

                        this.total_cnt = res.data.total
                        let tempArr = []
                        let tempArr1 = []
                        tempArr = res.data.result
                        for (var i = 0; i < tempArr.length; i++) {
                            let obj = {
                                state: false,
                                domain: tempArr[i].url,
                                label2: tempArr[i].label2,
                                label: tempArr[i].label,
                            }
                            tempArr1.push(obj)
                        }
                        this.tableData = []
                        this.tableData = tempArr1
                        console.log(this.tableData)
                    } else {

                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        assembleArr() {
            let tempArr = []
            let tempArr1 = []
            tempArr = JSON.parse(localStorage.getItem('tempUrlArr'))
            for (var i = 0; i < tempArr.length; i++) {
                let obj = {
                    state: false,
                    domain: tempArr[i].url,
                    label2: tempArr[i].label2,
                    label: tempArr[i].label,
                }
                tempArr1.push(obj)
            }
            this.tableData = []
            this.tableData = tempArr1

        },
        //重置
        reset() {
            this.value = "";
            this.value1 = "";
        },
     
 
        //单挑修改
        handleClick(inds, row) {
            if (row.state == false) {
                this.tableData[inds].state = true;
            } else {
                this.tableData[inds].state = false;
                let tempArr = []
                let tempArr1 = []
                tempArr[0] = row.domain
                tempArr[1] = row.label
                tempArr[2] = row.label2
                tempArr1.push(tempArr)
                let param = {
                    data_count: 1,
                    data_array: tempArr1
                }
                modify_label(param).then(res => {

                    console.log(res)
                    if (res.status == 0) {
                        this.$message({
                            type: "success",
                            message: "修改成功!"
                        });

                    }
                }).catch(error => {
                    console.log(error)
                })

            }
        },
        //批量修改
        onSubmit() {
            console.log(this.tableData)
            let tempArr = []
            for (var i = 0; i < this.tableData.length; i++) {
                let nowArr = []
                nowArr[0] = this.tableData[i].domain
                nowArr[1] = this.tableData[i].label
                nowArr[2] = this.tableData[i].label2
                tempArr.push(nowArr)
            }
            let arrLength = this.tableData.length
            let param = {
                data_count: arrLength,
                data_array: tempArr
            }
            modify_label(param).then(res => {
                console.log(res)
                if (res.status == 0) {
                    this.$message({
                        type: "success",
                        message: "修改成功!"
                    });

                }
            }).catch(error => {
                console.log(error)
            })

            console.log(tempArr)

        },

        // 表头样式设置
        headClass() {
            return "text-align: center;background:#eef1f6;";
        },
        // 表格样式设置
        rowClass() {
            return "text-align: center;";
        }
    },
    components: {
        fenye
    },
};
</script>

<style lang="scss" scoped>
.content {
    .seach_bottom {
        text-align: left;
        background: #ffffff;
        border-radius: 10px;
        padding: 10px 15px;
        display: flex;
        justify-content: flex-start;

        span {
            width: 80px;
            display: flex;
            align-items: center;
        }

        .el-select {
            width: 150px;
        }
    }
}
</style>



