<template>
<div class="content bath_m">
    <div class="top_title">
        <span @click="goback" style="font-size: 24px;color: #202020;"><i class="el-icon-arrow-left" style="color:#297AFF;font-size: 18px;margin-right:23px;font-weight: 600;"></i>
            配置</span>
    </div>
    <div class="bath" style="width: 1240px;margin: auto;background: #ffffff;margin-top: 25px;border-radius: 2px;padding: 15px;box-shadow:0px 2px 3px 0px rgba(6,17,36,0.14);">
        <el-tabs :tab-position="tabPosition" v-model="oneName" @tab-click="handleClick">
            <el-tab-pane name="zero">
                <p class="bath_title_btn" slot="label">
                    <i class="el-icon-arrow-left"></i>返回域名列表
                </p>
            </el-tab-pane>
            <div class="tala_title">
                <span>{{ this.nowfrom.url }}</span>
                <div>
                    <el-button style="background:#E05050;color:#ffffff;width:76px;height:34px;line-height: 10px;">停用</el-button>
                    <el-button style="width:76px;height:34px;line-height: 10px;" type="primary" @click="onsubmit">保存</el-button>
                </div>
            </div>
            <el-tab-pane label="基础配置" name="one">

                <div style="font-weight: 600;font-size: 18px;text-align: left;margin: 10px 0;">
                    基础信息
                </div>
                <ol class="tala">
                    <li>
                        <span>创建时间</span>
                        <span class="tala_con">{{ this.nowfrom.create_time }}</span>
                    </li>
                    <li>
                        <span>域名地址</span>
                        <span class="tala_con">{{this.nowfrom.url}}</span>
                    </li>
                    <li>
                        <span>主标签</span>
                        <span class="tala_con" v-if="!xas">{{ this.nowfrom.label }}</span>
                        <el-input v-model="main_tag" placeholder="请输入主标签" v-else style="width:23%;"></el-input>
                        <span class="tala_x" @click="xsho">{{
                xas == false ? "修改" : "确定"
              }}</span>
                    </li>
                    <li>
                        <span>副标签</span>
                        <el-select v-model="valueActive" placeholder="请选择" :disabled="!fas">
                            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" class="tala_con"></el-option>
                        </el-select>
                        <span class="tala_x" @click="fsho">{{
                fas == false ? "修改" : "确定"
              }}</span>
                    </li>
                </ol>
            </el-tab-pane>
            <el-tab-pane label="回源配置" name="two">
                <div class="talb">

                    <div style="font-weight: 600;font-size: 18px;text-align: left;margin: 10px 0;">
                        回源HOST
                    </div>
                    <div class="talb_title_tio">
                        <span>回源HOST</span>
                        <div>
                            <el-switch v-model="valuek" active-color="#13ce66" inactive-color="#EEEEEE" @change="changeSwitch"></el-switch>
                            <p>自定义CDN节点回源过程中所需访问的WEB服务器域名</p>
                        </div>
                    </div>
                    <div v-if="urlno">
                        <div class="talb_title_two">
                            <span>回源域名地址</span>
                            <span>URL：{{ this.backUrl }}</span>
                            <!-- <span>主标签：{{ dynamicValidateForm.region }}</span><span>副标签：{{ dynamicValidateForm.gesg }}</span> -->
                            <span class="tala_x" @click="xzurl">修改</span>
                        </div>
                        <!-- <div v-if="dynamicValidateForm.domains" class="talb_title_two" v-for="(item, index) in dynamicValidateForm.domains" v-bind:key="index">
                            <span>回源域名地址{{ index }}</span>
                            <span>URL：{{ item.email }}</span>
                            <span>主标签：{{ item.region }}</span><span>副标签：{{ item.gesg }}</span>
                            <span class="tala_x" @click="xfurl(index)">修改</span>
                        </div> -->
                    </div>

                </div>
            </el-tab-pane>

            <el-tab-pane label="缓存配置" name="there">

                <el-tabs v-model="activeName">
                    <el-tab-pane label="缓存设置" name="first">
                        <div class="talb_title_tio">
                            <span>缓存自动刷新</span>
                            <div>
                                <el-switch v-model="valueh" active-color="#13ce66" inactive-color="#EEEEEE" @change="changeSwitch"></el-switch>
                                <p>
                                    开启后，能够自动发现加速节点的同名缓存刷新，并自动同步最新缓存
                                </p>
                            </div>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="缓存过期时间" name="second">
                        <div>
                            <el-table :data="huancun" border :cell-style="rowClass" :header-cell-style="headClass">
                                <el-table-column prop="url" label="地址">
                                </el-table-column>
                                <el-table-column prop="expire_time" label="过期时间">
                                </el-table-column>
                                <el-table-column prop="weight" label="权重">
                                </el-table-column>
                                <!-- <el-table-column prop="hcstatus" label="状态">
                                </el-table-column> -->
                                <el-table-column prop="oper" label="操作">
                                    <template slot-scope="scope">
                                        <el-button @click="updatehc(scope.row)" type="text" size="small">修改</el-button>
                                        <!-- <el-button type="text" size="small" style="color:#000000" @click="delhc(scope.row)">删除</el-button> -->
                                    </template>
                                </el-table-column>
                            </el-table>
                        </div>
                    </el-tab-pane>
                </el-tabs>
            </el-tab-pane>
            <el-tab-pane label="自定义页面" name="four">

                <div style="text-align: left;margin: 15px 0;" class="customize">
                    <el-button type="primary" style="width:100px;" @click="dialogFormVisible = true">添加</el-button>

                </div>

                <div>
                    <el-table :data="tableData" stripe style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass">
                        <el-table-column prop="errCode" label="错误码"> </el-table-column>
                        <el-table-column prop="url" label="链接"> </el-table-column>
                        <el-table-column label="状态">
                            <template slot-scope="scope">
                                <span style="color:#E54545" v-if="scope.row.state == '已停止'">已停止</span>
                                <span style="color:#0ABF5B" v-else>启动中</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作">
                            <template slot-scope="scope">
                                <el-button @click="handleOnClick(scope.$index,scope.row)" type="text" size="small">{{
                      scope.row.status == "0" ? "启动" : "停止"
                    }}</el-button>
                                <el-button type="text" size="small" @click.native.prevent="deleteRow(scope.$index, tableData)" style="color:#000000">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </el-tab-pane>
        </el-tabs>
    </div>
    <el-dialog title="请输入回源域名地址" :visible.sync="dialogVisible" width="630px">
        <!-- 分割 -->
        <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="bath_demo_dynamic">
            <el-form-item prop="email" label="回源地址" :rules="{
                    required: true,
                    message: '域名不能为空',
                    trigger: 'blur'
                  }">
                <el-input v-model="backUrl" placeholder="请输入会员域名地址"></el-input>
                <!-- <el-select v-model="dynamicValidateForm.region" placeholder="线路">
                                    <el-option label="主" value="1"></el-option>
                                </el-select>
                                <el-select v-model="dynamicValidateForm.gesg" placeholder="格式">
                                    <el-option label="mp4" value="1"></el-option>
                                    <el-option label="flv" value="2"></el-option>
                                    <el-option label="avi" value="3"></el-option>
                                </el-select> -->
                <!-- <el-button @click="addDomain" type="text" size="small">
                                    添加
                                </el-button> -->
            </el-form-item>
            <!-- <el-form-item v-for="(domain, index) in dynamicValidateForm.domains" :label="'域名地址' + index" :key="domain.key" :prop="'domains.' + index + '.value'" :rules="{
                    required: true,
                    message: '域名不能为空',
                    trigger: 'blur'
                  }">
                                <el-input v-model="domain.value" placeholder="请输入会员域名地址"></el-input>
                                <el-select v-model="domain.region" placeholder="线路">
                                    <el-option label="副" value="1"></el-option>
                                     <el-option label="副" value="2"></el-option>
                                </el-select>
                                <el-select v-model="domain.gesg" placeholder="格式">
                                    <el-option label="mp4" value="1"></el-option>
                                    <el-option label="flv" value="2"></el-option>
                                    <el-option label="avi" value="3"></el-option>
                                </el-select>
                                 <el-button @click.prevent="removeDomain(domain)" type="text" size="small">
                                    删除
                                </el-button>
                            </el-form-item> -->
        </el-form>
        <div style="display: flex;justify-content: center;">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="onSubmitBack">确 定</el-button>
        </div>
        <!-- 分割 -->

    </el-dialog>
    <el-dialog title="请修改过期时间和权重" :visible.sync="dialogVisible1" width="630px">
        <!-- 分割 -->
        <el-form :model="dynamicValidateForm1" ref="dynamicValidateForm" label-width="100px" class="bath_demo_dynamic">
            <el-form-item prop="email" label="回源地址" :rules="{
                    required: true,
                    message: '域名不能为空',
                    trigger: 'blur'
                  }">
                <el-input v-model="weight" placeholder="请输入权重"></el-input>
            </el-form-item>
            <el-form-item label="活动时间">

                <el-date-picker v-model="valueTime" type="datetime" placeholder="选择日期时间">
                </el-date-picker>

            </el-form-item>

        </el-form>
        <div style="display: flex;justify-content: center;">
            <el-button @click="dialogVisible1 = false">取 消</el-button>
            <el-button type="primary" @click="onSubmitBack1">确 定</el-button>
        </div>
        <!-- 分割 -->

    </el-dialog>
    <!-- 添加弹窗 -->
    <el-dialog title="添加自定义页面" :visible.sync="dialogFormVisible">
        <el-form :model="form">
            <el-form-item label="错误码" :label-width="formLabelWidth">
                <el-input v-model="errorCode" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="链接地址" :label-width="formLabelWidth">
                <el-input v-model="errorUrl" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="addOwn">确 定</el-button>
        </div>
    </el-dialog>
</div>
</template>

<script>
import {
    query_url,
    config_url,
    delete_url

} from "../../servers/api";
import {
    add_url
} from "../../servers/api";
export default {
    data() {
        return {
            errorCode: "",
            errorUrl: "",
            valueTime: "",
            setstate: false,
            urlname: "165165165URL",
            tabPosition: "left",
            create_time: "2019-11-05 15:30:21",
            url: "http://www.xxxxx.cn",
            main_tag: "unite-1a",
            xas: false,
            fas: false,
            dialogVisible: false,
            dialogVisible1: false,
            urlno: false,
            dialogFormVisible: false,
            oneName: "one",
            activeName: "first",
            huiinput: "",
            formLabelWidth: "120px",
            huancun: [{
                hcaddress: "http://www.360.com",
                hctime: "2018-09-03",
                hcweig: 18,
                hcstatus: "异常"
            }],
            dynamicValidateForm: {
                domains: [{
                    email: "",
                    region: "",
                    gesg: ""
                }],
                email: "1212",
                region: "45454",
                gesg: "7856786"
            },
            dynamicValidateForm1: {
                domains: [{
                    email: "",
                    region: "",
                    gesg: ""
                }],
                email: "1212",
                region: "45454",
                gesg: "7856786"
            },
            mv1: "",
            mv2: "",
            region1: "1",
            region2: "",
            form: {
                name: "",
                url: ""
            },
            options: [{
                    value: 1,
                    label: "1"
                },
                {
                    value: 2,
                    label: "2"
                }
            ],
            tableData: [],

            value: "",
            valuek: false,
            valueh: false,
            valuet: false,
            valuez: false,
            valueActive: "",
            urlLinks: "",
            backUrl: "",
            weight: "",
            nowfrom: {
                create_time: "",
                url: "",
                label: "",
                label2: "",
                host_url: {
                    url: "",
                    valid: ""
                },
                cache_config: {
                    valid: "",
                    weight: "",
                    expire_time: ""
                },
                custom_page: []
            }
        };
    },
    mounted() {
        let tempUrl = localStorage.getItem('tempUrl')
        if (tempUrl) {
            this.urlLinks = tempUrl
        } else {
            this.urlLinks = ""
        }
        this.urlname = this.$route.query.urlress;
        // this.geturlconfig();
        this.queryUrlList()
    },
    methods: {
        //删除
        deleteRow(index, rows) {
            rows.splice(index, 1);
        },
        //添加自定义页面
        addOwn() {
            this.dialogFormVisible = false
            let obj = {
                errCode: parseInt(this.errorCode),
                url: this.errorUrl,
                state: parseInt("0")
            }
            this.tableData.push(obj)
        },
        //回源地址域名
        onSubmitBack() {
            this.dialogVisible = false
            this.nowfrom.host_url.url = this.backUrl
        },
        //缓存配置提交
        onSubmitBack1() {
            this.dialogVisible1 = false
            this.nowfrom.cache_config.weight = this.weight
            if (this.valueTime) {
                this.nowfrom.cache_config.expire_time = Math.floor(this.valueTime.getTime() / 1000)
            } else {
                this.nowfrom.cache_config.expire_time = ""
            }

        },
        //提交
        onsubmit() {
            this.nowfrom.custom_page = this.tableData
            console.log(this.nowfrom)
            this.nowfrom.cache_config.weight = parseInt(this.nowfrom.cache_config.weight)

            let param = new Object()
            let data_array = []
            data_array.push(this.nowfrom)
            param.data_count = 10
            param.data_array = data_array

            config_url(param).then(res => {
                if (res.status == 0) {
                    this.$message({
                        message: "修改成功",
                        type: "success"
                    });
                }

            }).catch(error => {
                console.lgo(error)
            })
        },
        //获取URL列表
        queryUrlList() {
            let params = new Object();
            params.page = 0
            params.buser_id = ""
            params.url = this.urlLinks

            //params.state=0
            // params.end_time=123456
            //  params.start_time=23456
            query_url(params)
                .then(res => {
                    console.log(res);
                    if (res.status == 0) {
                        this.tableData = res.data.result
                        this.total_cnt = res.data.total
                        this.nowfrom.url = res.data.result[0].url;
                        this.main_tag = res.data.result[0].label;
                        this.nowfrom = res.data.result[0]
                        this.main_tag = res.data.result[0].label
                        this.valueActive = res.data.result[0].label2
                        if (res.data.result[0].host_url.valid == 0) {
                            this.valuek = false
                        } else {
                            this.valuek = true
                        }
                        if (res.data.result[0].cache_config.valid == 0) {
                            this.valueh = false
                        } else {
                            this.valueh = true
                        }
                        this.backUrl = res.data.result[0].host_url.url
                        this.weight = res.data.result[0].cache_config.weight
                        let obj = {
                            url: res.data.result[0].url,
                            expire_time: res.data.result[0].cache_config.expire_time,
                            weight: res.data.result[0].cache_config.weight
                        }
                        this.huancun = []
                        this.huancun.push(obj)
                        this.tableData = res.data.result[0].custom_page

                        // res.data.result[0].host_url.valid;
                        console.log(this.nowfrom)

                    } else {

                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //请求数据--获取配置信息
        geturlconfig() {
            let parmas = new Object();
            parmas.url = this.urlname;
            parmas.page = 0;
            parmas.buser_id = "";
            query_url(parmas)
                .then(res => {
                    console.log(res);
                    this.create_time = res.data.result[0].create_time;

                    this.value = res.data.result[0].label2;
                    this.dynamicValidateForm.email = res.data.result[0].host_url.url;
                    this.valuek = res.data.result[0].host_url.valid;
                    this.valueh = res.data.result[0].cache_config.valid;
                    let obj = {};
                    obj.expire_time = res.data.result[0].cache_config.expire_time;
                    obj.weight = res.data.result[0].cache_config.weight;
                    this.huancun.push(obj);
                    this.tableData = res.data.result[0].custom_page;
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //请求数据--url
        setqueryurl() {},
        goback() {
            this.$router.go(-1);
        },
        //修改主标签
        xsho() {
            this.xas = !this.xas;
            if (this.xas == false) {
                this.nowfrom.label = this.main_tag
                //this.
            }
        },
        //修改副标签
        fsho() {
            this.fas = !this.fas;
            if (this.fas == false) {
                alert("****")
                this.nowfrom.label2 = this.valueActive
            }
        },
        //修改回源配置-主
        xzurl() {
            console.log("zhu");
            this.dialogVisible = true;
        },
        //修改回源配置-副
        xfurl(data) {
            console.log("fu", data);
        },
        //删除回源地址
        removeDomain(item) {
            var index = this.dynamicValidateForm.domains.indexOf(item);
            if (index !== -1) {
                this.dynamicValidateForm.domains.splice(index, 1);
            }
        },
        //添加回源地址
        addDomain() {
            this.dynamicValidateForm.domains.push({
                value: "",
                key: Date.now()
            });
        },
        handleClick() {

        },
        handleOnClick(index, event) {

            if (event.status == 0) {

                this.tableData[index].status = 1
            } else {
                this.tableData[index].status = 0

            }

            console.log(this.tableData)
            // if (tab.name == "zero") {
            //     this.$router.push({
            //         path: "/domain_management"
            //     });
            // }
        },
        //回源配置switch
        changeSwitch() {
            if (this.valuek == true) {
                this.urlno = true;
                this.nowfrom.host_url.valid = 1
            } else {
                this.urlno = false;
                this.nowfrom.host_url.valid = 0
            }
        },
        //缓switch
        changeSwitch1() {
            if (this.valueh == true) {
                // this.urlno = true;
                this.nowfrom.cache_config.valid = 1
            } else {
                //this.urlno = false;
                this.nowfrom.cache_config.valid = 0
            }
        },
        //缓存删除
        updatehc(data) {
            this.dialogVisible1 = true
            console.log(data);
        },
        delhc(data) {
            console.log(data);
        },
        // 表头样式设置
        headClass() {
            return "text-align: center;background:#eef1f6;";
        },
        // 表格样式设置
        rowClass() {
            return "text-align: center;";
        }
    }
};
</script>

<style lang="scss" scoped>
.content {

    // width: 1240px;
    // margin: auto;
    // padding: 19px;
    // margin-top: 34px;
    // background: #ffffff;
    .bath_title_btn {
        font-size: 16px;

        i {
            font-size: 16px;
            font-weight: 600;
            margin-right: 5px;
        }
    }

    .tala_title {
        height: 50px;
        line-height: 50px;
        // background: #eeeeee;
        padding: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        span {
            color: #333333;
            font-size: 18px;
            font-weight: 600;
        }
    }

    .tala {
        li {
            height: 50px;
            line-height: 50px;
            text-align: left;

            span {
                margin-right: 25px;
            }

            .tala_con {
                display: inline-block;
                width: 200px;
            }
        }
    }

    .tala_x {
        margin-left: 10px;
        color: rgb(64, 158, 255);
    }

    .talb_title_tio {
        text-align: left;
        display: flex;

        span {
            margin-right: 45px;
        }
    }

    .talb_title_two {
        text-align: left;
        display: flex;
        margin: 15px 0;

        span {
            margin-right: 30px;
        }
    }
}
</style>



